@mixin onDesktopmin {
  @media (min-width: 744px) {
    @content;
  }
}

@mixin onDesktopmax {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin top-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &-phone {
    display: flex;
    align-items: center;
  }
}
