.recommend {
  &__photo--container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 32px;
  }

  &__photo {
    grid-column: 2 / 4;
    width: 100%;

    margin: 0 0 32px;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }

    @include onDesktopmin {
      grid-column:  2 / 4;

      &:hover {
        transform: scale(1.07);
      }
    }

    @include onDesktopmax {
      grid-column: 1 / 5;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  &__product {
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    color: #292929;

    margin: 0 0 8px;
  }

  &__plus {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #292929;

    margin: 0 0 12px;
  }

  &__price {
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    color: #292929;
  }
}
